.max-height { height: 100%; }

.no-margin { margin: 0; }

.no-padding { padding: 0; }

.hide { display: none; }

@media (min-width: 768px) {

  .onlyMobile {
    display: none;
  }
}

.onlyWeb {
  display: none;
}

@media (min-width: 768px) {

  .onlyWeb {
    display: inherit;
  }
}

/*
  //////////////////////////////////////////////////////////////////////////////
  // Break Lines
  //////////////////////////////////////////////////////////////////////////////
*/

.break-line {
  width: 100%
}

.break-line > br {
  content: "";
  height: 10px;
  display: block;
}

.break-line > hr {
  margin: 0;
  border: 1px solid #ccc;
  opacity: 0.5;
}

/*
    ---------------------------------------------------- Containers - 04.08.2017
*/

.modal-container {
  background-color: white;
  position: relative;
  padding: 60px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

@media (min-width: 1200px) {

  .modal-container {
    padding: 70px 0;
  }
}

.modal-responsive {
  width: 100%;
  max-width: 550px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

@media (min-width: 768px) {

  .modal-responsive {
    max-width: 750px;
  }
}

@media (min-width: 1200px) {

  .modal-responsive {
    max-width: 1200px;
  }
}

/*
    ------------------------------------------------------- Buttons - 24.07.2017
*/

.button {
  border: none;
  cursor: pointer;
  outline: none;
  position: relative
}

.button > span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: background-color 0.25s ease-out;
  transition: background-color 0.25s ease-out;
}

.button > span:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.buttonBig {
  padding: 1rem 2rem;
}

.buttonSmall {
  padding: 0.5rem 1rem;
}

.buttonRound {
  border-radius: 50px
}

.buttonRound > span {
  border-radius: 50px;
}

.buttonSquare {
  border-radius: 4px
}

.buttonSquare > span {
  border-radius: 4px;
}

.buttonCircle {
  border-radius: 100%
}

.buttonCircle > span {
  border-radius: 100%;
}

.buttonCircleBig {
  width: 50px;
  height: 50px;
  padding: 0;
}

.buttonCircleSmall {
  border-radius: 100%;
