/*
    --------------------------------------------------------- Links - 07.07.2017
*/

.link {
  outline: none;
  cursor: pointer;
  text-decoration: none !important;
}

.link-inline {
  color: #0695d6;
  font-weight: 400;
  margin: 0 auto 1rem;
