/*
    -------------------------------------------------- Share Module - 04.08.2017
*/

.modal-share .share-open-button > .hamburger-1, .modal-share .share-open-button > .hamburger-2, .modal-share .share-open-button > .hamburger-3 {

  width: 15px;
  height: 2px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: calc(-15px / 2);
  margin-top: calc(-2px / 2);
  -webkit-transition: -webkit-transform 200ms;
  transition: -webkit-transform 200ms;
  transition: transform 200ms;
  transition: transform 200ms, -webkit-transform 200ms;
}

.modal-share {

  position: absolute;
  padding: 0;
  margin: 0;
  width: calc(100% - 40px);
  height: 37px;
  top: 20px;
  right: 20px;
  -webkit-filter: url('#shadowed-goo');
          filter: url('#shadowed-goo');
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.modal-share .share-open {
    display: none
  }

.modal-share .share-open:checked + .share-open-button {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}

.modal-share .share-open:checked + .share-open-button .hamburger-1 {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.modal-share .share-open:checked + .share-open-button .hamburger-2 {
  -webkit-transform: scale(0.1, 1);
          transform: scale(0.1, 1);
}

.modal-share .share-open:checked + .share-open-button .hamburger-3 {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

.modal-share .share-open:checked ~ .share-item {
  opacity: 1;
}

.modal-share .share-open:checked ~ .share-item:nth-of-type(1) {
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transform: scale(0.9) translate3d(-60px, 0, 0);
          transform: scale(0.9) translate3d(-60px, 0, 0);
}

.modal-share .share-open:checked ~ .share-item:nth-of-type(2) {
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  -webkit-transform: scale(0.9) translate3d(-120px, 0, 0);
          transform: scale(0.9) translate3d(-120px, 0, 0);
}

.modal-share .share-open:checked ~ .share-item:nth-of-type(3) {
  -webkit-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transform: scale(0.9) translate3d(-180px, 0, 0);
          transform: scale(0.9) translate3d(-180px, 0, 0);
}

.modal-share .share-open:checked ~ .share-item {
  /* @for $i from 1 through 5 {
          &:nth-child(#{$i + 2}) {
            transition-duration: 0.3s + (0.1s * $i);
            transform: scale(0.9) translate3d(-60px * $i, 0, 0);
          }
        } */
}

.modal-share .share-open-button {

    position: absolute;
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    border-radius: 100%;
    z-index: 2;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    opacity: 0.5
  }

.modal-share .share-open-button:hover {
  opacity: 1;
}

.modal-share .share-open-button.hamburger-dark {
  border: 1px solid #767676;
}

.modal-share .share-open-button.hamburger-dark > span {
  background: #767676;
}

.modal-share .share-open-button.hamburger-light {
  border: 1px solid white;
}

.modal-share .share-open-button.hamburger-light > span {
  background: white;
}

.modal-share .share-open-button > .hamburger-1 {
  -webkit-transform: translate3d(0, -5px, 0);
          transform: translate3d(0, -5px, 0);
}

.modal-share .share-open-button > .hamburger-2 {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.modal-share .share-open-button > .hamburger-3 {
  -webkit-transform: translate3d(0, 5px, 0);
          transform: translate3d(0, 5px, 0);
}

.modal-share .share-item {

    width: 35px;
    height: 35px;
    position: absolute;
    line-height: 35px;
    margin-left: -35px;
    border-radius: 100%;
    text-align: center;
    -webkit-transition: all 0.6s cubic-bezier(0.1, 0.1, 0.2, 1);
    transition: all 0.6s cubic-bezier(0.1, 0.1, 0.2, 1);
    opacity: 0
  }

.modal-share .share-item:hover {
  background-color: silver;
  color: white;
}

